<!--导入文件的错误提示-->
<template>
  <Alert v-if="importErrorMsg.length>0 && importErrorMsg[0].msg !== undefined" type="warning" class="zd-common-pr0">
    <vue-scroll>
      <div class="zd-common-maxH200 zd-common-pr0">
        <div class="zd-common-pr16">
          <p v-for="item in importErrorMsg"><span v-if="item.row!==undefined">第{{ item.row }}行,第{{ item.col }}列：</span>{{
            item.msg }}</p>
        </div>
      </div>
    </vue-scroll>
  </Alert>
</template>

<script>
    export default {
      props:{
        importErrorMsg:{
          type:Array
        }
      },
    }
</script>

<style scoped>

</style>



