<!--添加物品-->
<template>
<div>
  <div class="zd-common-black-b zd-common-pt5 zd-common-pb5 zd-common-pl20">
    <Input class="zd-common-mr10 zd-common-input-h32 zd-common-w200" placeholder="请输入物品编码" :maxlength="30"
           v-model="code"></Input>
    <Input class="zd-common-mr10 zd-common-input-h32 zd-common-w200" placeholder="请输入物品名称" :maxlength="30"
     v-model="name"></Input>
    <Button type="primary" class="zd-common-h32 zd-common-lh8" @click="selectHandle" :loading="selectLoading">
      <span v-if="!selectLoading">查询</span>
      <span v-else>正在查询...</span>
    </Button>
  </div>
  <Row :gutter="16" class="zd-common-mt10">
    <Col span="5">
      <div class="zd-common-border">
        <h3 class="zd-common-h40 zd-common-lh40 zd-common-border-bottom1 zd-common-pl20">物品分类</h3>
        <vue-scroll>
          <Tree :data="data" @on-select-change="changeSlideTree" class="zd-cm-h287 zd-common-pl10 zd-common-pr10"></Tree>
        </vue-scroll>
      </div>
    </Col>
    <Col span="14">
      <div class="zd-common-border">
        <h3 class="zd-common-h40 zd-common-lh40 zd-common-border-bottom1 zd-common-pl20">物品档案</h3>
        <div class="zd-common-pl10 zd-common-pr10 zd-common-pt10 zd-common-pb10" style="height: 295px">
          <Table border :columns="addGoodsCols" :data="attrData" :height="275" :row-class-name="rowClassName"
                 @on-row-click="clickRowHandle"></Table>
        </div>
      </div>
    </Col>
    <Col span="5">
      <div class="zd-common-border">
        <h3 class="zd-common-h40 zd-common-lh40 zd-common-border-bottom1 zd-common-pl20">
          已选物品{{ selectedArr.length>0?"（"+selectedArr.length+"）":'' }}
        </h3>
        <vue-scroll>
          <ul class="zd-cm-h287" style="margin-top: 8px;">
            <li v-for="(item,index) in selectedArr" :key="item.id"
                class="zd-common-clearFix zd-common-pl10 zd-common-pr10 zd-common-pt10 zd-common-pb10 zd-cm-border-b-dash1">{{ item.name }} <Icon type="md-close" class="zd-common-fr zd-common-mt4 zd-common-cp zd-common-mr20" @click="delHandle(index)"/></li>
          </ul>
        </vue-scroll>
      </div>
    </Col>
  </Row>
</div>
</template>

<script>
    export default {
        props:{},
        data(){
          return {
            //添加物品
            data:[],
            addGoodsCols:[
              {
                type: 'index',
                title:'序号',
                align: 'center',
                width:65
              },
              {
                title: '名称',
                align: 'center',
                key: 'name',
              },
              {
                title: '编码',
                align: 'center',
                key: 'code',
                width:65
              },
              {
                title: '分类',
                align: 'center',
                key: 'classify',
              },
              {
                title: '品牌',
                align: 'center',
                key: 'brand',
              },
              {
                title: '规格/型号',
                align: 'center',
                key: 'model',
              },
              {
                title: '单位',
                align: 'center',
                key: 'unit',
                width:65
              }
            ],
            attrData:[],
            code:'',
            name:'',
            kindId:'',
            selectLoading:false,
            selectedArr:[],
            selectedArrId:[],
            type:''//0--入库  1--出库  2--申领
          }
        },
        created(){
          // this.getKind();
        },
        methods:{
          //获取分类
          getKind(type){
            this.type = type;
            this.getGoodsKindTreeByUseType(type).then((res)=>{
              this.recursionFnTwo(this.data,res.data);
              // this.getGoodsList(res.data[0].id,this.name,this.code);
              this.getGoodsList('latest',this.name,this.code,type);
            })
          },
          changeSlideTree(arr,item){
            if(item.selected){
              this.getGoodsList(item.id,this.name,this.code,this.type);
              this.kindId = item.id;
            }else{
              this.kindId = '';
              this.attrData = [];
            }
          },
          getGoodsList(id,name,code,type){
            this.getGoodsInfoByUseType(type,id,name,code).then((res)=>{
              this.attrData = [];
              let aData = res.data;
              if(aData && aData.length>0){
                aData.map((item)=>{
                  this.attrData.push(
                    {
                      id:item.id,
                      name:item.name,
                      code:item.code,
                      classify:item.goodsKind,
                      brand:item.brand,
                      model:item.size,
                      unit:item.unit,
                      number:null,
                      currentInventory:'',
                      price:null,
                      amount:null,
                      priceList:[]
                    }
                  );
                })
              }
            })
          },
          //选择
          clickRowHandle(row,index){
            if(this.selectedArrId.indexOf(row.id)>-1){
              let index = this.selectedArrId.indexOf(row.id);
              this.selectedArr.splice(index,1);
              this.selectedArrId.splice(index,1);
            }else{
              this.selectedArr.push(row);
              this.selectedArrId.push(row.id);
            }
          },
          rowClassName(row, index) {
            if (this.selectedArrId.indexOf(row.id)>-1) {
              return 'demo-table-info-row';
            }
            return '';
          },
          //删除
          delHandle(index){
            this.selectedArr.splice(index,1);
            this.selectedArrId.splice(index,1);
          },
          //查询
          selectHandle(){
            this.getGoodsList("",this.name,this.code,this.type);
          }
        }
    }
</script>

<style scoped>

</style>



